<script>
    export let github_user_gists = [];
    let response_error = false;
    let animating_out = false;
    import { onMount } from "svelte";
    import { fade, fly, crossfade } from 'svelte/transition';
    import { anim_duration, anim_shift, delay_mul } from "./$layout.svelte";
    onMount(async function() {
        const response = await fetch("https://api.github.com/users/andersama/gists", {cache: 'force-cache'});
        if (response.ok) {
            response_error = false;
            github_user_gists = await response.json();
        } else {
            response_error = true;
            github_user_gists = [];
        }
    });
</script>

<svelte:head>
	<title>Andersama | Gists</title>
</svelte:head>

<div class="container">
<div class="container absolute">
    {#if response_error}
        Some error occurred, sorry!
    {/if}
    {#each github_user_gists as github_gist, i}
        {#if github_gist.public}
            <div class="github_gist"
            in:fly="{{ x: anim_shift, duration: anim_duration, delay: 40 + i*delay_mul}}"
            out:fly="{{ x: -anim_shift, duration: anim_duration, delay: i*delay_mul}}"
            >
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" width="16" height="16"><path fill-rule="evenodd" d="M3.75 1.5a.25.25 0 00-.25.25v11.5c0 .138.112.25.25.25h8.5a.25.25 0 00.25-.25V6H9.75A1.75 1.75 0 018 4.25V1.5H3.75zm5.75.56v2.19c0 .138.112.25.25.25h2.19L9.5 2.06zM2 1.75C2 .784 2.784 0 3.75 0h5.086c.464 0 .909.184 1.237.513l3.414 3.414c.329.328.513.773.513 1.237v8.086A1.75 1.75 0 0112.25 15h-8.5A1.75 1.75 0 012 13.25V1.75z"></path></svg>
                <a href="{github_gist.html_url}" alt="">
                {#if github_gist.description}
                    {github_gist.description}
                {:else}
                    {Object.keys(github_gist.files)[0]}
                {/if}
                </a>
            </div>
        {/if}        
    {/each}
</div>
</div>

<style>
    a {
        width: 250px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .absolute {
        position: absolute;
    }
</style>